@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@200;300;400;600&display=swap');
.banner-container{
   width: 100%;
    height:100vh;
    display: flex;
    justify-content: center;
    align-items:center;
    text-align: center;
    background: linear-gradient(rgba(0,0,0,0.4),rgba(0,0,0,0.6)), url('https://cdn.pixabay.com/photo/2017/01/22/19/20/pizza-2000614_1280.jpg');
    background-position:center;
    background-size: cover;
    position: relative;
    /* background-attachment: fixed; */
}

.container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 70%;
    text-align:left;
    height: 300px;
}

.welcome{
  color:#F2CE1B ;
  font-family:'Caveat', cursive;
  letter-spacing: 3px;
  font-size: 30px;
  font-weight: lighter;
}
.intro-message{
    color:aliceblue;
    font-family: 'Josefin Sans', sans-serif;
    font-weight:700;
    word-spacing: 5px;
    font-size: 3.3rem;
}
.Pizza-factory{
    color:#F2CE1B;
}
.intro-desc{
    color: white;
    font-family:'Nunito', sans-serif;
    font-weight: 200;
    word-spacing: 5px;
    font-size: 1.4rem;
    margin-top: 10px;
}
.cart-h2{
    position: absolute;
    top:80px;
    left:450px;
    font-family: var(--heading-font);
}