.About-container{
    display: flex;
    height: 400px;
    /* border: 1px solid lawngreen; */
}
.about-left{
    width: 50%;
    background-image: url('https://images.pexels.com/photos/262047/pexels-photo-262047.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940');
    background-position: center;
    background-size: cover;
    /* border: 1px solid red; */
}
.about-right{
    width: 40%;
    padding: 30px;
    margin-top:30px;
    margin-left: 30px;
    color: white;
}
.about-right-part{
    width: 40%;
}
.about-name{
    font-family: var(--heading-font);
    margin-bottom: 26px;
    font-size: 30px;
}
.about-i{
    color:var(--primary-color)
}
.about-para{
    font-family: var(--para-font);
    line-height: 24px;
    font-weight: 100;
    font-size: 15px;
}
