.nav-bar{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 65px;
    width: 100%;
    background-color: rgba(0,0,0,0.7);
    position: fixed;
    z-index: 999;
}
.navigation-links{
    margin-right:40px;
}
.navigation-links ul{
    display: flex;
    justify-content: center;
    align-items: center;
}

.navigation-links ul li{
    list-style-type: none;
}
.navigation-links ul li a{
    list-style-type: none;
    margin-right: 25px;
    letter-spacing: 1px;
    font-family: var(--para-font);
    font-size: 14px;
    color: white;
    text-decoration: none;
}

.navigation-links ul li a:hover{
    color:var(--primary-color);
    transition: 0.2s ease-in;
    cursor: pointer;
}

.logo{
    margin-left:60px;
    cursor: pointer;
}
.pizza{
    font-family: var(--heading-font);
    font-size: 29px;
    margin-left: 5px;
    color: white;
}
.Factory{
    font-family: var(--para-font);
    font-weight: 400;
    letter-spacing: 1.5px;
    color:var(--primary-color)
}
.menu-nav{
    color: white;
    text-decoration: none;
}
.menu-nav:hover{
    color:var(--primary-color);
    transition: 0.2s ease-in;
    cursor: pointer;
}
.cartCount{
    display:inline-block;
    background-color: var(--primary-color);
    color:black;
    width: 20px;
    height: 20px;
    text-align: center;
    border-radius: 10px;
}