.menu{
    width: 100%;
    border:  1px solid green;
    position: relative;
    display: block;
    color: white;
}
.menu-container{
    /* border: 2px solid yellow; */
    position: absolute;
    width: 100%;
    top: 100px;
    height: auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
}
a{
    color: transparent;
}
.pizza-image{
    width:210px;
    height:300px;
    /* border: 1px solid red; */
}
.pizza-container{
    width: 370px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid gainsboro;
    margin-bottom: 25px;
}
.order-price{
    font-family:var(--heading-font) ;
    font-size: 15px;
    color: var(--primary-color);
    margin-bottom:12px ;
}
.pizza-info{
    padding: 18px;
}
.pizza-name{
    font-family: var(--heading-font);
    margin-bottom: 13px;
    font-size: 20px;
}
.pizza-desc{
    font-family: var(--para-font);
    margin-bottom: 15px;
    font-size: 14px;
    color:gainsboro;
    font-weight: 200;
    line-height: 23px;
}
.order-btn{
    border: 1px solid var(--background-color);
    /* border: 1px solid white; */
    width: fit-content;
    padding: 7px 15px;
    font-family: var(--para-font);
    font-size: 15px;
    cursor: pointer;
}
.order-btn:hover{
    color:black;
    background-color: var(--background-color);
    transition: 0.4s ease-in-out;
    /* border: none; */
}
