.form-container{
    display: flex;
    justify-content: space-between;
    /* border:2px solid red */
}
.maps{
    width: 50%;
}
iframe{
    width: 613px;
    height: 455px;
}
.forms{
    display: block;
    width: 50%;
    background: black;
    padding: 50px;
    height: 100%;
}
label{
    color: white;
    display: block;
    font-family:  var(--para-font);
}  
.contact-name{
    color: white;
    font-family: var(--para-font);
    margin-bottom: 20px;
    font-size: 35px;
    margin-bottom: 35px;
}
input{
    display: block;
    margin-bottom: 40px;
    width: 400px;
    height: 40px;
    border: none;
    border-bottom: 1px solid white;
    background: transparent;
    outline: none;
    color: white;
}

.form-btn{
    padding: 10px 15px;
    background-color:var(--primary-color);
    border: none;
    font-size: 11px;
    cursor: pointer;
}
.form-btn:hover{
    background-color: var(--background-color);
}
::placeholder{
    color:white;
    letter-spacing: 1px;
    margin-left: 5px;
}