
.cart-container{
    width:100%;
    margin-top: 70px;
    padding: 0 100px;
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    font-family: var(--para-font);
}

.cart-details{
    width: 75%;
    /* border: 1px solid red; */
}

.cart{
    display: flex;
    align-items: center;
    background-color: white;
    /* background:linear-gradient(rgba(0,0,0,0.5),rgba(0,0,0,0.5)),url('') ; */
    padding: 15px 20px;
    /* border: 1px solid green; */
    position: relative;
}

.cart-image{
    display: inline-block;
    width: 80px;
    height: 110px;
    border-radius: 5px;
    overflow: hidden;
}

.cart-image img{
    width: 100%;
    height: 100%;
}

.cart-product-name{
    display: inline-block;
    width: 150px;
    padding: 0 15px;
    font-weight: bold;
    font-size: 14px;
    font-family: var(--para-font);
}

.cart-product-price{
    display: inline-block;
    width: 120px;
    padding:0 15px;
    text-align: center;
    font-weight: bold;
    font-size: 14px;
}

.inc{
    display: inline-block;
    width: 40px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    font-size:14px;
    color:var(--mainColor);
    border-radius: 50%;
    cursor: pointer;

}

.product-quantity{
    display: inline-block;
    padding:0 15px;
    font-size: 14px;
    width: 70px;
    text-align: center;
}

.dec{
    display: inline-block;
    width: 40px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    /* border: 1px solid rgb(192, 51, 51); */
    font-size:14px;
    color:rgb(192, 51, 51);
    border-radius: 50%;
    cursor: pointer;

}

.product-total-price{
    display: inline-block;
    width: 170px;
    padding: 0 15px;
    text-align: center;
    font-size: 14px;
}

.delete-product{
    border: none;
    color: rgb(192, 51, 51);
    cursor: pointer;
}

/* Cart Summary */

.cart-summary{
    /* border: 1px solid red; */
    width: 25%;
    padding: 0 0 0 25px;
    position: absolute;
    right:20px;
    bottom:20px;
    
}

.summary{
    background: var(--whiteColor);
    padding: 20px;
}

.summary h3{
    font-size: 18px;
    font-weight: normal;
    border-bottom: 1px solid #999;
}

.total-items{
    display: flex;
    margin: 15px 0;
}

.items{
    width: 50%;
    text-align: left;
    font-weight: normal;
}

.items-count{
    width: 50%;
    text-align:  right;
    font-weight: bold;
    color:var(--mainColor)
}
.total-price-section{
    display: flex;
    margin: 15px 0px;
}

.just-title{
    width: 50%;
    text-align: left;
    font-weight: normal;
}

.items-price{
    width: 50%;
    text-align: right;
    font-weight: bold;
    color: var(--mainColor)
}