.call-container{
    height: 300px;
    flex-direction: column;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    color: white;
    background: linear-gradient(rgba(0,0,0,0.5),rgba(0,0,0,0.5)),url('https://cdn.pixabay.com/photo/2014/07/08/12/34/pizza-386717_960_720.jpg');
    background-position:center;
    background-size: cover;
    background-attachment: fixed;
}

.special{
    font-family: var(--heading-font);
    font-size: 40px;
    padding:30px;
    margin-top:30px;
}
.special-p{
    font-family: var(--para-font);
    padding: 20px;
    color:white;
}
.special-btn{
    padding: 8px 15px;
    color:white;
    background-color: var(--primary-color);
    font-family: var(--para-font);
    border: none;
}
.special-btn:hover{
    background-color: var(--background-color);
}