.service-heading{
    text-align: center;
    padding:40px;
    margin:30px;
    font-family: var(--heading-font);
    font-size: 20px;
    margin-top: 30px;
}

.serve{
    width: 250px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    border:2.5px solid white;
    border-radius: 15px;
    padding: 24px;
    box-shadow: 0 0 20px #000
}

.service-card{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    margin-top: 85px;
    
    /* height: 300px; */
}
.service-logo{
    padding: 20px;
    font-size: 50px;
}
.service-name{
    margin: 10px;
    font-family: var(--heading-font);
    font-size: 18px;
    font-weight:  600px;;
}
.service-p{
    font-family: var(--para-font);
    font-weight: 500;
    font-size: 14px ;
}