.details-container{
    display: flex;
    justify-content: space-between;
    font-family: var(--heading-font);
   
}
.details-left{
    display: flex;
    width: 60%;
    padding: 40px;
    color: white;
    background-color: black;
}
.details-card{
    display: flex;
    width: 450px;
    /* border: 1px solid green; */
}
.details-icon{
    color:var(--primary-color);
    margin-right: 16px;
   
}

.details-h4{
    margin-bottom: 5px;
}
.details-p{
    margin-top: 15px;
    font-weight: 200;
    font-size: 15px;
}
.details-right{
    color:white;
    background-color: var(--primary-color) ;
    /* border: 1px solid red;*/
    width:40% ;
    position: relative;
}
.details-icons {
    position: absolute;
    top:60px;
    margin-left: 80px;
    cursor: pointer;
}

.details-icons i{
    margin-right:30px;
    font-size:1.1rem;
}

